import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import moment from 'moment';
import { AngularFirestore } from 'angularfire2/firestore';
import { ExamResults, Collections, StatView, User } from '../../app/app.models';
import { map, tap } from 'rxjs/operators';
import { averageMultiplier } from '../../app/app.config';
import { AuthProvider } from '../../providers/auth/auth';
import { Observable } from 'rxjs';
import { sortBy } from 'lodash';
import { StatsProvider } from '../../providers/stats/stats';

@IonicPage()
@Component({
  selector: 'page-mi-analitica',
  templateUrl: 'mi-analitica.html',
})
export class MiAnaliticaPage {

  public promedioMes: number;
  public promedioGeneral: number;

  public timeline$: Observable<StatView[]>;
  public lowAverageList: any[];
  public struct$: Observable<StatView[]>;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private auth: AuthProvider,
    private stats: StatsProvider,
  ) {
  }

  ionViewDidLoad() {
    this.loadInfo();
  }

  loadInfo() {
    this.auth.user$.subscribe(user => {
      if (user) {
        this.calculateAverage(user.uid);
        this.calculateAverageGeneral(user.uid);
        this.loadTimeline();
        this.loadLowAverage(user);
        this.loadStruct();
      }
    })
  }

  calculateAverage(uid: string) {

    const start = moment().startOf('month').toISOString();
    const end = moment().endOf('month').toISOString();

    this.afs.collection<ExamResults>(Collections.EXAM_RESULT, ref => ref
      .where('user', '==', uid)
      .where('date', '>=', start)
      .where('date', '<=', end))
      .valueChanges()
      .pipe(
        map(results => {
          const total = results.length;
          return results.map((r: ExamResults) => r.promedio).reduce((a, b) => a + b, 0) / total * averageMultiplier;
        }),
        tap(async (average: number) => {
          this.promedioMes = average;
          this.afs.doc(`${Collections.USER_STAT}/stat-${uid}`).update({ monthAverage: average });
        })
      ).subscribe();

  }

  calculateAverageGeneral(uid: string) {

    this.afs.collection<ExamResults>(Collections.EXAM_RESULT, ref => ref.where('user', '==', uid))
      .valueChanges()
      .pipe(
        map(results => {
          const total = results.length
          return results.map((r: ExamResults) => r.promedio).reduce((a, b) => a + b, 0) / total * averageMultiplier
        }),
        tap(async (average: number) => {
          this.promedioGeneral = average
          this.afs.doc(`${Collections.USER_STAT}/stat-${uid}`).update({ generalAverage: average })
        })
      ).subscribe()

  }

  loadTimeline() {
    this.timeline$ = this.afs.collection<StatView>(Collections.STAT_VIEW, ref => ref.where('isTimeline', '==', true)).valueChanges()
  }

  async loadLowAverage(user: User) {
    const list = await this.stats.computeUserAverageList(user)
    await this.afs.doc(`${Collections.USER}/${user.uid}`).update({ average_list: list })
    this.lowAverageList = sortBy(list.filter(t => t.promedio <= 0.6 && t.promedio > 0), 'promedio');
    console.log(this.lowAverageList);
  }

  loadStruct() {
    this.struct$ = this.afs.collection<StatView>(Collections.STAT_VIEW, ref => ref
      .where('isTimeline', '==', false))
      .valueChanges()
      .pipe(
        map(views => views.filter(v => !v.parent)),
      );
  }

}



